
<template>
  <div class="metadata-management-statistics-history">
    <div>
      <gs-form
        ref="queryForm"
        inline
      >
        <gs-form-item>
          <gs-button
            @click="goback"
          >返回上一级</gs-button>
        </gs-form-item>
        <gs-form-item
          label=""
          prop="filename"
        >
          <gs-date-picker
            :clearable="false"
            v-model="dateRange.value"
            type="date-range"
          />
        </gs-form-item>
        <gs-form-item>
          <gs-button
            type="primary"
            @click="search"
          >查询</gs-button>
        </gs-form-item>
      </gs-form>
    </div>
    <el-radio-group v-model="tabPosition">
      <el-radio-button label="1">日</el-radio-button>
      <el-radio-button label="2">月</el-radio-button>
      <el-radio-button label="3">年</el-radio-button>
    </el-radio-group>
    <el-tabs
      v-model="activeName"
      type="card"
    >
      <el-tab-pane
        label="表格展示"
        name="tableList"
      >
        <table-list
          v-loading="isLoading"
          ref="table-list"
          :data-list="dataList"
          :total="total"
          @search="search"
        />
      </el-tab-pane>
      <!-- <el-tab-pane
        label="走势图"
        name="trend"
      >
        <trend-chart />
      </el-tab-pane> -->
    </el-tabs>
  </div>
</template>

<script>
import './history.scss';
import moment from 'moment';
import TableList from './table-list.vue';
import TrendChart from './trend-chart.vue';
import * as statisticsService from 'src/service/metadata-management/statistics';
export default {
  components: {
    TableList,
    TrendChart
  },
  data() {
    return {
      isLoading: false,
      activeName: 'tableList',
      tabPosition: '1',
      dataList: [],
      total: 0,
      dateRange: {
        value: [moment().subtract(1, 'week'), moment()]
      }
    };
  },
  watch: {
    tabPosition: function (newQuestion, oldQuestion) {
      this.search();
    }
  },
  mounted() {
    this.search();
  },
  methods: {
    /**
     * 查询
     */
    search() {
      if (this.activeName === 'tableList') {
        const paginationParam = this.$refs['table-list'].getPaginationParam();
        this.getTableStatHistory({
          'tableId': this.$route.params.id,
          'dateType': this.tabPosition,
          'fromDate': this.dateRange.value[0].format('YYYY-MM-DD'),
          'endDate': this.dateRange.value[1].format('YYYY-MM-DD'),
          ...paginationParam
        });
      }
    },
    getTableStatHistory(params) {
      this.isLoading = true;
      statisticsService.getTableStatHistory(params).then((res) => {
        this.isLoading = false;
        this.dataList = res.list;
        this.total = res.total;
      }).catch((res) => {
        this.isLoading = false;
      });
    },
    /**
     * 返回上一级
     */
    goback() {
      this.$emit('back');
    },
    /**
     * 查看历史信息
     */
    viewHistory() {
    }
  }
};
</script>




