
<template>
  <div class="metadata-management-relationship clearfix">
    <div
      class="svg-wrap"
    >
      <div
        v-loading="isSvgLoading"
        class="relative svg-content"
      >
        <table-relation-chart
          ref="tableRelationChart"
          :current-selected="currentSelected.guid"
          :guid="guid"
          @selectChange="changeSelectItem"
        />
      </div>
    </div>
    <div
      v-loading="isDetailLoading"
      class="relation-ship-detail pull-right"
    >
      <div class="relative">
        <h3 class="text-center relationship-title">表详情</h3>
        <gs-button
          :disabled="viewRelationship"
          type="text-primary"
          class="relationship-btn"
          @click="loadSelectRelationship"
        >血缘图</gs-button>
      </div>
      <div class="relationship-item">
        <h4>表名称</h4>
        <i
          class="iconfont icon-biaoge"
        />
        <span>{{ detailInfo.tableName }}</span>
      </div>
      <div class="relationship-item">
        <h4>所属数据库</h4>
        <i
          class="iconfont icon-yuanshuju"
        />
        <span>{{ detailInfo.dbName }}</span>
      </div>
      <div class="relationship-item">
        <h4>直接上游表数量</h4>
        <span>{{ detailInfo.directUpStreamNum }}</span>
      </div>
      <div class="relationship-item">
        <h4>直接下游表数量</h4>
        <span>{{ detailInfo.directDownStreamNum }}</span>
      </div>
      <div class="relationship-item">
        <h4>上游表层数</h4>
        <span>{{ detailInfo.upStreamLevelNum }}</span>
      </div>
      <div class="relationship-item">
        <h4>下游表层数</h4>
        <span>{{ detailInfo.downStreamLevelNum }}</span>
      </div>
      <div class="relationship-item">
        <h4>表结构更新时间（DDL）</h4>
        <span>{{ detailInfo.tableUpdateTime }}</span>
      </div>
    </div>
  </div>
</template>

<script>
import './relationship.scss';
import * as relationshipService from 'src/service/metadata-management/relationship';
import TableRelationChart from './tableRelationChart.vue';
export default {
  components: {
    TableRelationChart
  },
  data() {
    return {
      isDetailLoading: false,
      isSvgLoading: false,
      detailInfo: {},
      data: [],
      currentSelected: {
        guid: this.$route.params.id
      },
      tipFun: null
    };
  },
  computed: {
    viewRelationship() {
      return this.$route.params.id === this.currentSelected.guid;
    },
    guid() {
      return this.$route.params.id;
    }
  },
  mounted() {
    this.getData();
  },
  methods: {
    /**
     * 获取页面数据
     */
    getData() {
      this.getTableLineageInfo();
    },
    /**
     * 修改当前选中元素
     */
    changeSelectItem(id) {
      this.currentSelected.guid = id;
      this.getTableLineageInfo();
    },
    /**
     * 加载选中元素的表血缘关系
     */
    loadSelectRelationship() {
      this.$router.replace({
        path: `/main/metadata/detail/${this.currentSelected.guid}`,
        query: {
          module: 'relationship'
        }
      }, () => {
        this.$refs['tableRelationChart'].getTableLineage();
        this.$emit('refreshTableInfo');
      });
    },
    /**
     * 获取表血缘详情
     */
    getTableLineageInfo() {
      this.isDetailLoading = true;
      const id = this.currentSelected.guid;
      relationshipService.getTableLineageInfo(this.currentSelected.guid).then((res) => {
        // 重复请求处理
        if (id !== this.currentSelected.guid) {
          return;
        }
        this.isDetailLoading = false;
        this.detailInfo = res;
      }).catch((res) => {
        this.isDetailLoading = false;
      });
    }
  }
};
</script>




